import { Injectable, OnInit } from '@angular/core';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs/Observable';
import * as NebPay from 'nebpay';
import * as nebulas from 'nebulas';


@Injectable()
export class UserService implements OnInit {
  private nebPay;
  private neb;
  private dAppAddress = environment.dAppAddress;
  private intervalQuery:number;

  constructor() {
    this.nebPay = new NebPay();
    this.neb = new nebulas.Neb()
  }

  ngOnInit() {
  }

  createAuthor(params: any) {
    let that = this,
      value = "0",
      callFunction = "createAuthor",
      callArgs = JSON.stringify([params.name, params.url, params.signature]);

    this.neb.setRequest(new nebulas.HttpRequest(environment.http_request_url));

    let obs = new Observable(observer => {
      this.nebPay.call(this.dAppAddress, value, callFunction, callArgs, {
        listener: function (res) {
          console.log("交易提交成功");
          let txHash = res.txhash;

          that.intervalQuery = setInterval(function () {
            that.neb.api.getTransactionReceipt(txHash).then(function (res) {
              console.log(res);
              if (res.status === 1) {
                clearInterval(that.intervalQuery);
                console.log("交易完成");
                observer.complete();
              }

              if (res.status === 0) {
                clearInterval(that.intervalQuery);
                observer.error(res.execute_result);
              }
            });
          }, 5000);
        }
      });
    });

    return obs;
  }

  getAuthor():Observable<any> {
    let value = "0",
      nonce = "0",
      gas_price = "1000000",
      gas_limit = "2000000",
      contract = {
        "function": "getAuthorInfo",
        "args": "[]"
      },
      that = this,
      obs = new Observable(observer => {
        this.neb.setRequest(new nebulas.HttpRequest(environment.http_request_url));
        this.nebPay.simulateCall(this.dAppAddress, "0", 'getUserAddr', "", {
          listener: function (res) {
            let result = res.result,
              curUser: string;

            if (result === 'null') {
              observer.error("似乎没有安装钱包插件，请先装插件再使用");
            } else {
              try {
                curUser = JSON.parse(result);
                that.neb.api.call(curUser, that.dAppAddress, value, nonce, gas_price, gas_limit, contract).then(function (res) {
                  console.log("读取信息成功");
                  observer.next(res.result);
                }).catch(function () {
                  observer.error("读取博客列表失败");
                });
              } catch (err) {
                observer.error("获取用户钱包地址出错");
              }
            }
          }
        });
      });

    return obs;
  }

}



