import { Injectable, OnInit } from '@angular/core';
import { environment } from '../environments/environment';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

import * as NebPay from 'nebpay';
import * as nebulas from 'nebulas';


@Injectable()
export class BlogService implements OnInit {
  private currentBlog:any;
  private nebPay;
  private neb;
  private dAppAddress = environment.dAppAddress;
  private intervalQuery: number;

  constructor() {
    this.nebPay = new NebPay();
    this.neb = new nebulas.Neb();
  }

  ngOnInit() {
    this.neb.setRequest(new nebulas.HttpRequest(environment.http_request_url));
  }

  createPost(params:any):Observable<any> {
    this.neb.setRequest(new nebulas.HttpRequest(environment.http_request_url));

    let that = this,
      value = "0",
      callFunction = "createPost",
      callArgs = JSON.stringify([params.title, params.content, params.cat]),
      obs = new Observable(observer => {
        this.nebPay.call(this.dAppAddress, value, callFunction, callArgs, {
          listener: function (res) {
            console.log("交易提交成功");
            let txHash = res.txhash;

            that.intervalQuery = setInterval(function () {
              that.neb.api.getTransactionReceipt(txHash).then(function (res) {
                console.log(res);
                if (res.status === 1) {
                  clearInterval(that.intervalQuery);
                  console.log("交易完成");
                  observer.complete();
                }

                if (res.status === 0) {
                  clearInterval(that.intervalQuery);
                  observer.error(res.execute_result);
                }
              });
            }, 5000);
          }
        });
      });

    return obs;
  }

  getAllPost():Observable<any> {
    let from = 'n1JjwbAQAqr6cV6btULFhP6596o3KkwNSVj',
      value = "0",
      nonce = "0",
      gas_price = "1000000",
      gas_limit = "2000000",
      contract = {
        "function": "getAllPost",
        "args": "[]"
      },
      that = this,
      obs = new Observable(observer => {
        this.neb.setRequest(new nebulas.HttpRequest(environment.http_request_url));
        that.neb.api.call(from, that.dAppAddress, value, nonce, gas_price, gas_limit, contract).then(function (res) {
          console.log("读取信息成功");
          console.log(res.result);
          observer.next(res.result);
        }).catch(function () {
          observer.error("读取博客列表失败");
        });
      });

    return obs;
  }

  getAuthorPosts():Observable<any> {
    let value = "0",
      nonce = "0",
      gas_price = "1000000",
      gas_limit = "2000000",
      contract = {
        "function": "getAuthorPosts",
        "args": "[]"
      },
      that = this,
      obs = new Observable(observer => {
        this.neb.setRequest(new nebulas.HttpRequest(environment.http_request_url));
        this.nebPay.simulateCall(this.dAppAddress, "0", 'getUserAddr', "", {
          listener: function (res) {
            let result = res.result,
              curUser: string;

            if (result === 'null') {
              observer.error("似乎没有安装钱包插件，请先装插件再使用");
            } else {
              try {
                curUser = JSON.parse(result);
                that.neb.api.call(curUser, that.dAppAddress, value, nonce, gas_price, gas_limit, contract).then(function (res) {
                  console.log("读取信息成功");
                  observer.next(res.result);
                }).catch(function () {
                  observer.error("读取博客列表失败");
                });
              } catch (err) {
                observer.error("获取用户钱包地址出错");
              }
            }
          }
        });
      });

    return obs;
  }

  sendPay(params:any): Observable<any> {
    let that = this,
      value = "0",
      callFunction = "sendPay",
      callArgs = JSON.stringify([params.fromName, params.pid, params.comment]),
      obs = new Observable(observer => {
        this.nebPay.call(this.dAppAddress, value, callFunction, callArgs, {
          listener: function (res) {
            console.log("交易提交成功");
            let txHash = res.txhash;

            that.intervalQuery = setInterval(function () {
              that.neb.api.getTransactionReceipt(txHash).then(function (res) {
                if (res.status === 1) {
                  clearInterval(that.intervalQuery);
                  console.log("交易完成");
                  observer.next(res);
                }
              });
            }, 5000);
          }
        });
      });

    return obs;
  }

  setCurrentBlog(blog) {
    this.currentBlog = blog;
  }

  getCurrentBlog() {
    return this.currentBlog;
  }
}



