import { Component, OnInit, ApplicationRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';

import { BlogService } from '../blog.service';
import { NotificationComponent } from '../notification/notification.component';
import { BlogComponent } from '../blog/blog.component';

declare const $: any;
declare const tinymce: any;

@Component({
  providers: [NotificationComponent, BlogComponent],
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  allBlog = [];

  constructor(private blogService: BlogService,
              private notification: NotificationComponent,
              private ref: ApplicationRef,
              private router: Router) { }

  ngOnInit() {
    this.getAllPost();
  }

  getAllPost() {
    this.blogService.getAllPost().subscribe(result => {
      this.allBlog = JSON.parse(result).reverse();
      console.log(this.allBlog[0]);
      this.ref.tick();
      $(".blog.segment").removeClass("loading");
    }, error => {
      this.notification.show(error);
      $(".blog.segment").removeClass("loading");
    }, () => $(".blog.segment").removeClass("loading"));
  }

  showCreateTag() {
    $(".ui.tag.sidebar").sidebar("toggle");
  }

  onSendPay(sendPayForm: NgForm, blog, event) {
    if (sendPayForm.valid) {
      let params = sendPayForm.value,
        target = $(event.target),
        payItems = blog.payItems;

      params.pid = blog.pid;
      target.addClass("loading");
      this.blogService.sendPay(params).subscribe(
        value => {
          sendPayForm.reset();
          target.removeClass("loading");
          params.from = value.from;
          params.ct = new Date().getTime();
          payItems.push(params);
          this.ref.tick();
        },
        error => {},
        () => {}
      )
    }
  }

  showBlogDetail(blog) {
    this.blogService.setCurrentBlog(blog);
    this.router.navigate(['blog'])
  }
}



