import { Component, OnInit, NgZone } from '@angular/core';
import { BlogService } from '../blog.service';
import { Router } from '@angular/router';


import { NotificationComponent } from '../notification/notification.component';


declare const $:any;


@Component({
  providers: [NotificationComponent],
  selector: 'app-mine',
  templateUrl: './mine.component.html',
  styleUrls: ['./mine.component.css']
})
export class MineComponent implements OnInit {
  allBlog:any;

  constructor(private blogService:BlogService,
              private notification:NotificationComponent,
              private ngZone: NgZone,
              private router: Router) {
  }

  ngOnInit() {
    this.getAllPost();
  }

  getAllPost() {
    this.blogService.getAuthorPosts().subscribe(result => {
      this.ngZone.runOutsideAngular(() => {
        this.ngZone.run(() => {
          this.allBlog = JSON.parse(result).reverse();
        });
      });

      $(".blog.segment").removeClass("loading");
    }, error => {
      this.notification.show(error);
      $(".blog.segment").removeClass("loading");
    }, () => $(".blog.segment").removeClass("loading"));
  }

  showBlogDetail(blog) {
    this.blogService.setCurrentBlog(blog);
    this.router.navigate(['blog']);
  }
}



