import {Component, OnInit} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {UserService} from './user.service';
import {NgForm} from '@angular/forms';
import {NotificationComponent} from './notification/notification.component';


declare const $: any;

@Component({
  providers: [NotificationComponent],
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  menuIndex = 1;

  constructor(
    private userService: UserService,
    private notification: NotificationComponent,
    private router: ActivatedRoute) {

  }

  ngOnInit() {
    let pathName = window.location.pathname,
        pathMap = {"/": 1, "/home": 1, "/mine": 2, "/post": 3};

    this.menuIndex = pathMap[pathName];
  }

  showRegister() {
    $(".ui.register.sidebar").sidebar("toggle");
  }

  showSendPay(pid) {
    $(".ui.sendpay.sidebar").sidebar("toggle");
  }

  onRegister(regForm: NgForm) {
    if (regForm.valid) {
      let params = regForm.value;

      params.url = "https://api.adorable.io/avatars/285/" + params.author + ".png";
      this.userService.createAuthor(params).subscribe(value => {

      }, error => {
        this.notification.show(error);
        regForm.reset();
      }, () => {
        this.notification.show("用户注册成功");
        regForm.reset();
        this.showRegister();
      });
    }
  }
}



